import React, { useEffect } from "react";
import editProfilePen from "../images/pen.svg";
import plusSign from "../images/plussign.svg";
import Card from "./Card";
import api from "../utils/api";
import {
  CurrentUserContext,
  CardsContext,
} from "../contexts/CurrentUserContext";
function Main(props) {
  const currentUser = React.useContext(CurrentUserContext);
  const initalCards = React.useContext(CardsContext);
  const [userData, setUserData] = React.useState([]);
  const [cards, setCards] = React.useState([]);
  function handleCardLike(card) {
    const isLiked = card.likes.some((i) => i._id === userData._id);

    api.changeLikeCardStatus(card._id, !isLiked).then((newCard) => {
      setCards((state) => state.map((c) => (c._id === card._id ? newCard : c)));
    });
  }
  function handleCardDelete(card) {
    api.deleteCard(card._id);
    const deleteId = card._id;
    const newCardArr = [];
    cards.filter((card) => {
      if (!(card._id == deleteId)) {
        newCardArr.push(card);
      }
    });
    setCards(newCardArr);
  }

  useEffect(() => {
    currentUser
      .then((res) => {
        setUserData(res);
      })
      .catch((err) => {
        console.log(err);
      });

    initalCards
      .then((res) => {
        setCards(res);
      })
      .catch((err) => {
        console.log(err);
      });
  }, []);

  return (
    <main>
      <section className="profile">
        <div className="profile__container">
          <div
            onMouseEnter={props.onProfileOver}
            onMouseLeave={props.onProfileLeave}
            className="profile__image-container"
          >
            <img
              className="profile__image"
              src={userData.avatar}
              alt="profile image"
            />
            <img
              src={editProfilePen}
              alt="editImagePen"
              className={`profile__image-pen ${
                props.isOpen ? `profile__image-pen-show` : ""
              }`}
              id="pen"
              onClick={props.onEditAvatarClick}
            />
          </div>
          <div className="profile__text">
            <div className="profile__title">
              <h1 className="profile__username" id="fullName">
                {userData.name}
              </h1>
              <button
                type="button"
                id="openProfile"
                className="profile__edit"
                onClick={props.onEditProfileClick}
              >
                <img
                  className="profile__pen"
                  src={editProfilePen}
                  id="profilePen"
                  alt="pen"
                />
              </button>
            </div>
            <p className="profile__subtitle" id="description">
              {userData.about}
            </p>
          </div>
        </div>
        <button
          type="button"
          className="profile__button"
          id="addNewContentButton"
          onClick={props.onAddPlaceClick}
        >
          <img
            className="profile__plus"
            src={plusSign}
            id="plusSign"
            alt="plus sign"
          />
        </button>
      </section>

      <section className="elements">
        {cards.map((card) => (
          <Card
            card={card}
            key={card._id}
            fetchData={props.getCardData}
            currentUserId={userData._id}
            onCardLike={handleCardLike}
            onCardDelete={handleCardDelete}
          />
        ))}
      </section>
    </main>
  );
}

export default Main;
